.table-wrapper {
    display: flex;
    flex-direction:column ;
    padding: 32px 0 0 32px;
}

header {
    position: fixed;
    z-index: 3;
    left: 0;
    right: 0;
    top: 0;
}

.table{
    background: #201F2E;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 28px;
    min-width: 320px;
}

.table-wrapper-text {
    border: none;
    background: transparent;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
}

.table-wrapper-date {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    color: #C4C1CF;
}

.logo {
    padding: 32px 32px 0 0;
}

.logo-two {
    margin-left: 45px;
}

.logo-three {
    position: absolute;
    margin-left: 24px;
}

nav {
    Background: linear-gradient(
    180deg, #201F2E 36%, rgba(32, 31, 46, 0) 100%);
    display: flex;
    overflow: auto;
    height: 60px;
    align-items: flex-start;
    padding-left: 13px;
    cursor: pointer;
    margin: -1px;
    height: 42px;
  }
  
.nav-button {
    width: 109px;
    height: 40px;
    line-height: 21px;
    font-size: 18px;
    background-color: transparent;
    color: #C4C1CF;
    padding: 0 16px;
    font-style: normal;
    font-weight: 300;
    font-family: 'Roboto';
    border: 1px solid transparent;
    margin: 0 4px;
  }
  
.nav-button:hover,
.nav-button-active {
    border: 1px solid #C4C1CF;
    border-radius: 32px;
    cursor: pointer;
}